* {
    text-decoration: none;
}

Link:hover {
    text-decoration: none;
}

.version {
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 8px;
    background-color: var(--negro);
    color: var(--blanco);
    font-size: 12px;
    z-index: 9999;
}

:root {
    --primario: rgb(225, 159, 255);
    --gris: rgb(80, 60, 80);
    --secundario: rgb(61, 0, 62);
    --claro: rgb(190, 190, 190);
    --blanco: #fff;
    --negro: #333;
    font-family: "Roboto", sans-serif;
}

body::-webkit-scrollbar {
    width: .8rem;
    background-color: var(--negro);
}

body::-webkit-scrollbar-thumb {
    width: .8rem;
    background-color: var(--gris);
    border-radius: .3rem;
}

footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--gris);
    color: var(--blanco);
    width: 100%;
    height: 5rem;
    font-size: 18px;
    font-weight: 400;
}