.main {
    width: 100%;
    margin: 0;
    background-color: var(--negro);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;
}

.main-me-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    width: 60%;
    height: auto;
    margin-top: 2rem;
    margin-bottom: 3rem;
}

.main-img-container {
    width: 30%;
    max-width: 300px;
    height: auto;
    overflow: hidden;
    border: 1px solid var(--blanco);
    border-radius: 50%;
}

.main-img {
    width: 100%;
    height: auto;
    transition: .3s ease-in-out;
}

.main-img:hover {
    transform: scale(1.3);
    filter: saturate(110%);
}

.main-text-container {
    width: 60%;
}

.main-name {
    margin: 0rem;
    color: var(--blanco);
    font-weight: 600;
    font-size: 36px;
    margin-bottom: 2rem;
    text-wrap: pretty;
    margin-left: 1rem;
}

.main-text {
    margin: 0rem;
    color: var(--claro);
    font-weight: 200;
    font-size: 18px;
    font-weight: 400;
    text-wrap: pretty;
    margin-left: 1rem;
}

span {
    color: var(--primario);
    font-weight: 400;
}


.main-btn-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    width: 60%;
    height: auto;
    margin-top: 1rem;
    margin-bottom: 2rem;
    margin-left: 1rem;
}

.main-btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: auto;
    height: auto;
    background-color: var(--blanco);
    color: var(--negro);
    font-size: 16px;
    font-weight: 500;
    padding: .3rem;
    padding-right: .8rem;
    border-radius: 2rem;
    transition: .3s ease-in-out;
}

.main-btn:hover {
    filter: saturate(250%);
    cursor: pointer;
    box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);

}

.main-btn img {
    width: 1.5rem;
    height: 1.5rem;
    margin: 0;
    margin-right: .2rem;
}
