.proyectos {
    width: 65%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    color: var(--blanco);
    font-weight: 600;
    margin-bottom: 2rem;
}

.card {
    width: 100%;
    max-width: 900px;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 1rem;
    background-color: var(--gris);
    color: var(--negro);
    padding: 1rem;
    margin: 1rem;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.6);
    transition: .3s ease-in-out;
}

.card:hover {
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.6);

}

.card-text-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 50%;
}


.card-title {
    margin: 0rem;
    margin-left: .5rem;
    font-size: 28px;
    text-wrap: pretty;
    color: var(--blanco);
    margin-bottom: 1rem;
}

.card-title span {
    color: var(--gris);
    font-weight: 600;
}

.card-desc {
    color: var(--claro);
    margin: 0rem;
    margin-left: .5rem;
    font-size: 15px;
    text-wrap: pretty;
    margin-bottom: 2rem;
}

.card-desc span {
    color: var(--primario);
    font-weight: 600;
}

.card-img-container {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 40%;
    height: 100%;
    margin-left: 1rem;
    overflow: hidden;
    border-radius: .5rem;
    border: 1px solid var(--blanco);
    transition: .3s ease-in-out;
}

.card-img {
    width: 100%;
    height: auto;
    margin: 0;
    margin-right: .2rem;
    transition: .3s ease-in-out;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: .3s ease-in-out;
}

.card-img-container:hover .overlay {
    opacity: 1;
}


.card-img-container:hover {
    box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
}

.overlay span {
    color: var(--negro);
    font-size: 18px;
    font-weight: bold;
}

.card-pill-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: auto;
    gap: .5rem;
    margin-left: 1rem;
}

.card-pill {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: .2rem .6rem;
    background-color: var(--blanco);
    color: var(--negro);
    border-radius: 1rem;
    font-size: 14px;
    transition: .3s ease-in-out;
}

.card-pill:hover {
    cursor: pointer;
    box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
}

.card-pill-img {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: .5rem;
    border-radius: 50%;
}