.header {
    background-color: var(--negro);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 3rem;
    top: 0;
    left: 0;
    z-index: 9999;
    transition: .3s ease-in-out;
    padding: 0;
    margin: 0;
    gap: 1rem;
    position: sticky;
}

.header-home-img {
    width: 2rem;
    height: 2rem;
    margin-right: 2rem;
    transition: .3s ease-in-out;
}

.header-btn {
    width: auto;
    height: auto;
    background-color: var(--negro);
    color: var(--blanco);
    text-align: center;
    padding: .8rem 2.5rem;
    margin: auto;
    font-weight: 500;
    border-radius: 1rem;
    transition: .3s ease-in-out;
}

.header-btn:hover {
    background-color: rgb(28, 28, 28);
    cursor: pointer;
}
